import React, { Component } from 'react';
import './style.scss';

class index extends Component {
  render() {
    return (
      <div className="container">
        <div className="page red">1</div>
        <div className="page blue">2</div>
        <div className="page green">3</div>
        <div className="page red">4</div>
      </div>
    );
  }
}

export default index;
